.container {
	display: flex;
	justify-content: center;
	margin: 2rem 0;
}

.loader {
	text-align: center;
	width: 5rem;
	height: 5rem;
	border-radius: 50%;
	background: conic-gradient(#0000 10%, lightgrey);
	-webkit-mask: radial-gradient(
		farthest-side,
		#0000 calc(100% - 8px),
		#000 0
	);
	animation: loader 1.5s infinite linear;
}

@keyframes loader {
	to {
		transform: rotate(1turn);
	}
}
